\r\n$theme-light:#fff;\r\n$theme-dark:#212529;\r\n\r\n$skin1: #fff !important;//\r\n$skin2: #3d3d3d !important;///\r\n$skin6:$skin2;\r\n\r\n/*Topbar Colors*/\r\n\r\n$topbar:$skin2;\r\n$topbar-height:100px;\r\n$topbar-navlink-padding:0px 14px;\r\n$topbar-navlink-font-size:0.875rem;\r\n$topbar-navlink-height:100px;\r\n$topbar-navbrand-padding:0 10px 0 10px;\r\n\r\n/*Sidebar Colors*/\r\n\r\n$sidebar: $theme-light;\r\n$sidebar-alt:#e8eff0;\r\n$sidebar-text: #606060;\r\n$sidebar-icons: #606060;\r\n$sidebar-text-dark: #212529;\r\n$sidebar-icons-dark: #4F5467;\r\n\r\n$sidebar-width-full:250px;\r\n$sidebar-width-mini:65px;   \r\n\r\n/*Boxed layout width*/\r\n\r\n$boxed-width:1200px;\r\n\r\n/*Shadow*/\r\n\r\n$shadow:1px 0px 20px rgba(0, 0, 0, 0.08);\r\n\r\n/*transitions*/\r\n\r\n$transitions:0.2s ease-in;\r\n\r\n/*Dark transparent bg*/\r\n\r\n$transparent-dark-bg:rgba(0, 0, 0, 0.05);\r\n\r\n$lft:left;\r\n$rgt:right;\r\n$card-alt:#e4e9ef;\r\n\r\n%square {\r\n    border-radius: 0px;\r\n}\r\n\r\n%rotate45 {\r\n    transform: rotate(45deg);\r\n    -ms-transform: rotate(45deg);\r\n    -webkit-transform: rotate(45deg);\r\n    -o-transform: rotate(45deg);\r\n    -moz-transform: rotate(45deg);\r\n}\r\n\r\n/*******************************/\r\n// Bootstrap overrides\r\n/*******************************/\r\n\r\n/**\r\n * Table Of Content\r\n *\r\n *  1. Color system\r\n *  2. Options\r\n *  3. Body\r\n *  4. Typography\r\n *  5. Breadcrumbs\r\n *  6. Cards\r\n *  7. Dropdowns\r\n *  8. Buttons\r\n *  9. Typography\r\n *  10. Progress bars\r\n *  11. Tables\r\n *  12. Forms\r\n *  14. Component\r\n */\r\n\r\n//\r\n// Color system\r\n//\r\n$white: #fff !default;\r\n$gray-100: #f8f9fa !default;\r\n$gray-200: #e9ecef !default;\r\n$gray-300: #dee2e6 !default;\r\n$gray-400: #ced4da !default;\r\n$gray-500: #a1aab2 !default;\r\n$gray-600: #6c757d !default;\r\n$gray-700: #4F5467 !default;\r\n$gray-800: #343a40 !default;\r\n$gray-900: #212529 !default;\r\n$black: #000 !default;\r\n\r\n$blue: #2962FF !default;\r\n$indigo: #6610f2 !default;\r\n$purple: #7460ee !default;\r\n$pink: #e83e8c !default;\r\n$red: #f62d51 !default;\r\n$orange: #fb8c00 !default;\r\n$yellow: #ffbc34 !default;\r\n$green: #36bea6 !default;\r\n$teal: #20c997 !default;\r\n$cyan: #4fc3f7 !default;\r\n$primary: #7460ee !default;\r\n$text-muted: $gray-500 !default;\r\n\r\n$colors: ( blue: $blue,\r\nindigo: $indigo,\r\npurple: $purple,\r\npink: $pink,\r\nred: $red,\r\norange: $orange,\r\nyellow: $yellow,\r\ngreen: $green,\r\nteal: $teal,\r\ncyan: $cyan,\r\nwhite: $white,\r\ngray: $gray-600,\r\ngray-dark: $gray-800);\r\n\r\n$primary: $blue !default;\r\n$secondary: $gray-600 !default;\r\n$success: $green !default;\r\n$info: $blue !default;\r\n$warning: $yellow !default;\r\n$danger: $red !default;\r\n$light: $gray-100 !default;\r\n$dark: $gray-800 !default;\r\n$cyan: $cyan !default;\r\n$orange: $orange !default;\r\n$theme-colors: () !default;\r\n$theme-colors: map-merge(( \"primary\": $primary,\r\n\"secondary\": $secondary,\r\n\"success\": $success,\r\n\"info\": $info,\r\n\"warning\": $warning,\r\n\"danger\": $danger,\r\n\"light\": $light,\r\n\"dark\": $dark,\r\n\"cyan\": $cyan,\r\n\"orange\": $orange,\r\n\"purple\": $purple),\r\n$theme-colors);\r\n\r\n// Quickly modify global styling by enabling or disabling optional features.\r\n\r\n$enable-caret: true !default;\r\n$enable-rounded: true !default;\r\n$enable-shadows: false !default;\r\n$enable-gradients: false !default;\r\n$enable-transitions: true !default;\r\n$enable-hover-media-query: false !default;\r\n$enable-grid-classes: true !default;\r\n$enable-print-styles: true !default;\r\n\r\n// Body\r\n//\r\n// Settings for the  element.\r\n\r\n$main-body-bg: #fff!default;\r\n$body-bg: #3D3D3D;\r\n$body-color: #3e5569 !default;\r\n$grid-gutter-width: 20px !default;\r\n\r\n// Typography\r\n//\r\n// Font, line-height, and color for body text, headings, and more.\r\n\r\n$font-size-base: 0.875rem;\r\n$font-family-sans-serif: 'Nunito Sans',\r\nsans-seri