   }\r\n\r\n    // Bootstrap specific changes start\r\n\r\n    // Specify a size and min-width to make printing closer across browsers.\r\n    // We don't set margin here because it breaks `size` in Chrome. We also\r\n    // don't use `!important` on `size` as it breaks in Chrome.\r\n    @page {\r\n      size: $print-page-size;\r\n    }\r\n    body {\r\n      min-width: $print-body-min-width !important;\r\n    }\r\n    .container {\r\n      min-width: $print-body-min-width !important;\r\n    }\r\n\r\n    // Bootstrap components\r\n    .navbar {\r\n      display: none;\r\n    }\r\n    .badge {\r\n      border: $border-width solid $black;\r\n    }\r\n\r\n    .table {\r\n      border-collapse: collapse !important;\r\n\r\n      td,\r\n      th {\r\n        background-color: $white !important;\r\n      }\r\n    }\r\n\r\n    .table-bordered {\r\n      th,\r\n      td {\r\n        border: 1px solid $gray-300 !important;\r\n      }\r\n    }\r\n\r\n    .table-dark {\r\n      color: inherit;\r\n\r\n      th,\r\n      td,\r\n      thead th,\r\n      tbody + tbody {\r\n        border-color: $table-border-color;\r\n      }\r\n    }\r\n\r\n    .table .thead-dark th {\r\n      color: inherit;\r\n      border-color: $table-border-color;\r\n    }\r\n\r\n    // Bootstrap specific changes end\r\n  }\r\n}\r\n","/*******************\r\nPadding margin property \r\n*******************/\r\n\r\n$vertical-property-map: ( m-t-: margin-top, m-b-: margin-bottom, m-r-: margin-right, m-l-: margin-left, p-t-: padding-top, p-b-: padding-bottom, p-r-: padding-right, p-l-: padding-left, p-: padding, m-:margin);\r\n$sizes-list: 5 10 15 20 25 30 40;\r\n@each $size in $sizes-list {\r\n    $val: $size + 0px;\r\n    @each $keyword,\r\n    $property in $vertical-property-map {\r\n        html body .#{$keyword}#{$size} {\r\n            #{$property}: $val;\r\n        }\r\n    }\r\n}\r\n\r\n\r\n\r\n/** * Zero */\r\n\r\n$zero-property-map: ( m-0: margin, m-t: margin-top, m-r: margin-right, m-b: margin-bottom, m-l: margin-left, p-0: padding, p-t: padding-top, p-r: padding-right, p-b: padding-bottom, p-l: padding-left);\r\n@each $keyword,\r\n$property in $zero-property-map {\r\n    html body .#{$keyword}-0 {\r\n        #{$property}: 0;\r\n    }\r\n}","// Theme colors\r\n#main-wrapper{\r\n    &[data-layout=\"vertical\"]{\r\n        /*This is for the logo bg*/\r\n        .topbar .top-navbar .navbar-header{\r\n            &[data-logobg=\"skin1\"]{\r\n                background: $skin1;\r\n            }\r\n        }\r\n        .topbar .navbar-collapse, .topbar{\r\n            &[data-navbarbg=\"skin6\"]{\r\n                background: $skin6;\r\n            }\r\n        }\r\n        /*This is for the sidebar bg*/\r\n        .left-sidebar{\r\n            &[data-sidebarbg=\"skin1\"], &[data-sidebarbg=\"skin1\"] ul{\r\n                background: $skin1;\r\n            }\r\n        }\r\n    }\r\n}\r\n","// App\r\n\r\n/*============================================================== \r\n For all pages \r\n ============================================================== */\r\n\r\n#main-wrapper {\r\n    width: 100%;\r\n    overflow: hidden;\r\n    font-family: 'Poppins';\r\n}\r\n\r\n.page-wrapper {\r\n    background: $body-bg;\r\n    position: relative;\r\n    transition: 0.2s ease-in;\r\n}\r\n\r\n.page-wrapper>.container-fluid {\r\n    padding: 20px;\r\n    min-height: calc(100vh - 180px);\r\n}\r\n\r\n.page-wrapper-nopadding {\r\n    background: $body-bg;\r\n    position: relative;\r\n    transition: 0.2s ease-in;\r\n}\r\n\r\n.page-wrapper-nopadding>.container-fluid {\r\n    min-height: 100%;\r\n    padding: 0;\r\n}\r\n\r\n/*******************\r\n Footer\r\n*******************/\r\n\r\n.footer {\r\n    padding: 15px 20px;\r\n}\r\n\r\n/*******************\r\n Vertical layout\r\n*******************/\r\n\r\n#main-wrapper[data-layout=\"vertical\"] {\r\n    /*Vertical*/\r\n    .topbar .top-navbar .navbar-header {\r\n        //width: $sidebar-width-full;\r\n        transition: 0.2s ease-in;\r\n    }\r\n\r\n    /*sidebar type*/\r\n    &[data-sidebartype=\"full\"] {\r\n        .page-wrapper {\r\n            margin-left: $sidebar-width-full;\r\n        }\r\n        .page-wrapper-nopadding {\r\n            margin-left: $sidebar-width-full;\r\n        }\r\n    }\r\n    /*Sidebar position*/\r\n    &[data-sidebar-position=\"fixed\"] {\r\n        .topbar .top-navbar .navbar-header {\r\n            position: fixed;\r\n            z-index: 10;\r\n        }\r\n        .left-sidebar {\r\n            position: fixed;\r\n        }\r\n    }\r\n    /*Header position*/\r\n    &[data-header-position=\"fixed\"] {\r\n        .topbar {\r\n            position: fixed;\r\n            width: 100%;\r\n        }\r\n        .page-wrapper {\r\n            padding-top: $topbar-height;\r\n        }\r\n        .page-wrapper-nopadding {\r\n            padding-top: 0;\r\n        }\r\n    }\r\n}\r\n\r\n/*******************\r\n Vertical responsive layout\r\n*******************/\r\n\r\n@include media-breakpoint-up(md) {\r\n    #main-wrapper[data-layout=\"vertical\"] {\r\n\r\n        /*Vertical*/\r\n        &[data-sidebartype=\"full\"] {\r\n            .topbar .top-navbar .navbar-header {\r\n                width: $sidebar-width-full;\r\n            }\r\n        }\r\n        /*Sidebar position*/\r\n        &[data-sidebar-position=\"fixed\"][data-sidebartype=\"full\"] {\r\n            .topbar .top-navbar .navbar-collapse {\r\n                margin-left: $sidebar-width-full;\r\n            }\r\n        }\r\n        &[data-sidebar-position=\"fixed\"][data-sidebartype=\"mini-sidebar\"] {\r\n            .topbar .top-navbar .navbar-collapse {\r\n                margin-left: $sidebar-width-mini;\r\n            }\r\n        }\r\n        /*Vertical with mini-sidebar*/\r\n        &[data-sidebartype=\"mini-sidebar\"] {\r\n            .topbar .top-navbar .navbar-header {\r\n                width: $sidebar-width-mini;\r\n                .logo-text {\r\n                    display: none;\r\n                }\r\n                &.expand-logo {\r\n                    width: $sidebar-width-full;\r\n                    .logo-text {\r\n                        display: block;\r\n                    }\r\n                }\r\n            }\r\n        }\r\n        /*Fixed sidebar with minisidebar*/\r\n        &[data-sidebar-position=\"fixed\"][data-sidebartype=\"mini-sidebar\"] {\r\n            .topbar .top-navbar .navbar-collapse {\r\n                margin-left: $sidebar-width-mini;\r\n            }\r\n        }\r\n        &[data-sidebartype=\"mini-sidebar\"] {\r\n            .page-wrapper {\r\n                margin-left: $sidebar-width-mini;\r\n            }\r\n            .sidebar-nav {\r\n                .hide-menu,\r\n                .has-arrow:after {\r\n                    display: none;\r\n                }\r\n                .nav-small-cap {\r\n                    justify-content: center;\r\n                }\r\n            }\r\n            .left-sidebar {\r\n                width: $sidebar-width-mini;\r\n                &:hover {\r\n                    width: $sidebar-width-full;\r\n                    .sidebar-nav {\r\n                        .hide-menu,\r\n                        .has-arrow:after {\r\n                            display: block;\r\n                        }\r\n                        .nav-small-cap {\r\n                            justify-content: flex-start;\r\n                        }\r\n                    }\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n@media(max-width:767px) {\r\n    #main-wrapper {\r\n        &[data-sidebartype=\"mini-sidebar\"] {\r\n            .left-sidebar {\r\n                left: -$sidebar-width-full;\r\n            }\r\n        }\r\n        &.show-sidebar {\r\n            .left-sidebar {\r\n                left: 0;\r\n            }\r\n        }\r\n        &[data-layout=\"vertical\"][data-sidebar-position=\"fixed\"] {\r\n            .topbar .top-navbar {\r\n                .navbar-collapse {\r\n                    position: relative;\r\n                    top: $topbar-height;\r\n                }\r\n            }\r\n        }\r\n        /*Header position*/\r\n        &[data-layout=\"vertical\"][data-header-position=\"fixed\"][data-sidebar-position=\"fixed\"] {\r\n            .topbar .top-navbar {\r\n                .navbar-collapse {\r\n                    position: relative;\r\n                    top: $topbar-height;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}","// scafholding\r\n* {\r\n    outline: none;\r\n}\r\n\r\nbody {\r\n    margin: 0;\r\n    overflow-x: hidden;\r\n    color: $body-color;\r\n    background: $body-bg;\r\n}\r\n\r\nhtml {\r\n    position: relative;\r\n    min-height: 100%;\r\n}\r\n\r\na {\r\n    color: $themecolor;\r\n}\r\n\r\na:hover,\r\na:focus {\r\n    text-decoration: none;\r\n}\r\n\r\na.link {\r\n    color: $body-color;\r\n    &:hover,\r\n    &:focus {\r\n        color: $themecolor;\r\n    }\r\n}\r\n\r\n.box {\r\n    border-radius: $border-radius;\r\n    padding: 10px;\r\n}\r\n\r\n\r\n.no-wrap {\r\n    td,\r\n    th {\r\n        white-space: nowrap;\r\n    }\r\n}\r\n\r\n\r\n\r\n/*******************\r\nBlockquote\r\n*******************/\r\n\r\nhtml body blockquote {\r\n    border-left: 5px solid $primary;\r\n    border: 1px solid $border-color;\r\n    padding: 15px;\r\n}\r\n\r\n.clear {\r\n    clear: both;\r\n}\r\n\r\nol li {\r\n    margin: 5px 0;\r\n}\r\n\r\n\r\n/*******************\r\nThumb size\r\n*******************/\r\n\r\n.thumb-sm {\r\n    height: 32px;\r\n    width: 32px;\r\n}\r\n\r\n.thumb-md {\r\n    height: 48px;\r\n    width: 48px;\r\n}\r\n\r\n.thumb-lg {\r\n    height: 88px;\r\n    width: 88px;\r\n}\r\n\r\n.hide {\r\n    display: none;\r\n}\r\n\r\n.radius {\r\n    border-radius: $border-radius;\r\n}\r\n\r\n\r\n/*******************\r\nOpacity\r\n*******************/\r\n\r\n.op-5 {\r\n    opacity: 0.5;\r\n}\r\n\r\n.op-3 {\r\n    opacity: 0.3;\r\n}\r\n\r\n\r\n\r\n/*******************\r\nfont weight\r\n*******************/\r\n\r\nhtml body {\r\n    .font-bold {\r\n        font-weight: $font-weight-bold;\r\n    }\r\n    .font-normal {\r\n        font-weight: $font-weight-normal;\r\n    }\r\n    .font-light {\r\n        font-weight: $font-weight-light;\r\n    }\r\n    .font-medium {\r\n        font-weight: $font-weight-medium;\r\n    }\r\n    .font-16 {\r\n        font-size: 16px;\r\n    }\r\n    .font-12 {\r\n        font-size: 12px;\r\n    }\r\n    .font-14 {\r\n        font-size: 14px;\r\n    }\r\n    .font-10 {\r\n        font-size: 10px;\r\n    }\r\n    .font-18 {\r\n        font-size: 18px;\r\n    }\r\n    .font-20 {\r\n        font-size: 20px;\r\n    }\r\n    .font-22 {\r\n        font-size: 22px;\r\n    }\r\n    .font-24 {\r\n        font-size: 24px;\r\n    }\r\n    .display-5 {\r\n        font-size: 3rem;\r\n    }\r\n    .display-6 {\r\n        font-size: 2.5rem;\r\n    }\r\n    .display-7 {\r\n        font-size: 2rem;\r\n    }\r\n}\r\n\r\n\r\n\r\n/*******************\r\nBackground colors\r\n*******************/\r\n\r\nhtml body {\r\n    .bg-megna {\r\n        background-color: $cyan;\r\n    }\r\n    .bg-theme {\r\n        background-color: $themecolor;\r\n    }\r\n    .bg-inverse {\r\n        background-color: $gray-900;\r\n    }\r\n    .bg-purple {\r\n        background-color: $purple;\r\n    }\r\n    .bg-light {\r\n        background-color: $gray-200;\r\n    }\r\n    .bg-white {\r\n        background-color: $white;\r\n    }\r\n}\r\n\r\n\r\n\r\n/*******************\r\nRounds\r\n*******************/\r\n\r\n.round {\r\n    line-height: 48px;\r\n    color: $white;\r\n    width: 50px;\r\n    height: 50px;\r\n    display: inline-block;\r\n    font-weight: 400;\r\n    text-align: center;\r\n    border-radius: 100%;\r\n    background: $blue;\r\n    line-height: 52px;\r\n    img {\r\n        border-radius: 100%;\r\n    }\r\n    &.round-info {\r\n        background: $blue;\r\n    }\r\n\r\n    &.round-warning {\r\n        background: $warning;\r\n    }\r\n\r\n    &.round-danger {\r\n        background: $danger;\r\n    }\r\n\r\n    &.round-success {\r\n        background: $success;\r\n    }\r\n\r\n    &.round-primary {\r\n        background: $primary;\r\n    }\r\n}\r\n\r\n.round-lg {\r\n    line-height: 65px;\r\n    width: 60px;\r\n    height: 60px;\r\n    font-size: 30px;\r\n}\r\n\r\n\r\n\r\n\r\n\r\n/*******************\r\nLabels\r\n*******************/\r\n\r\n.label-rounded {\r\n    border-radius: 60px;\r\n}\r\n\r\n\r\n\r\n/*******************\r\n Badge\r\n******************/\r\n\r\n.badge {\r\n    font-weight: $font-weight-light;\r\n    line-height: normal;\r\n    &.badge-pill {\r\n        padding: 0.20em 0.6em;\r\n    }\r\n}\r\n\r\n.badge-xs {\r\n    font-size: 9px;\r\n}\r\n\r\n.badge-xs,\r\n.badge-sm {\r\n    -webkit-transform: translate(0, -2px);\r\n    -ms-transform: translate(0, -2px);\r\n    -o-transform: translate(0, -2px);\r\n    transform: translate(0, -2px);\r\n}\r\n\r\n\r\n\r\n/*******************\r\nList-style-none\r\n******************/\r\n\r\nul.list-style-none {\r\n    margin: 0px;\r\n    padding: 0px;\r\n    li {\r\n        list-style: none;\r\n        a {\r\n            color: $body-color;\r\n            padding: 8px 0px;\r\n            display: block;\r\n            text-decoration: none;\r\n            &:hover {\r\n                color: $themecolor;\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n\r\n/*******************\r\nCard\r\n******************/\r\n\r\n.card {\r\n    margin-bottom: 20px;\r\n    .card-subtitle {\r\n        font-weight: $font-weight-light;\r\n        margin-bottom: 10px;\r\n        color: $text-muted;\r\n    }\r\n    .card-title {\r\n        position: relative;\r\n        font-weight: $font-weight-medium;\r\n        margin-bottom: 10px;\r\n        color: rgb(1, 92, 7);\r\n    }\r\n    .card-actions {\r\n        float: $rgt;\r\n        a {\r\n            padding: 0 5px;\r\n            cursor: pointer;\r\n        }\r\n    }\r\n    .card-header .card-title{\r\n        margin-bottom: 0px;\r\n    }\r\n}\r\n\r\n.card-alt {\r\n    margin: 0 -20px;\r\n    background: $card-alt;\r\n}\r\n\r\n.card-group {\r\n    margin-bottom: 20px;\r\n    .card {\r\n        border-right: 1px solid $border-color;\r\n    }\r\n}\r\n\r\n.card-fullscreen {\r\n    position: fixed;\r\n    top: 0px;\r\n    left: 0px;\r\n    width: 100%;\r\n    height: 100%;\r\n    z-index: 9999;\r\n    overflow: auto;\r\n}\r\n\r\n.oh {\r\n    overflow: hidden;\r\n}\r\n.card-hover{\r\n    -webkit-transition: all .25s ease;\r\n    -o-transition: all .25s ease;\r\n    -moz-transition: all .25s ease;\r\n    transition: all .25s ease;\r\n    &:hover{\r\n        webkit-transform: translateY(-4px) scale(1.01);\r\n        -moz-transform: translateY(-4px) scale(1.01);\r\n        -ms-transform: translateY(-4px) scale(1.01);\r\n        -o-transform: translateY(-4px) scale(1.01);\r\n        transform: translateY(-4px) scale(1.01);\r\n        -webkit-box-shadow: 0 14px 24px rgba(62,57,107,.1);\r\n        box-shadow: 0 14px 24px rgba(62,57,107,.1);\r\n    }\r\n}\r\n.draggable-cards .card-header{\r\n    cursor: move;\r\n}\r\n.card-moved .card{\r\n    background: $info;\r\n    color:$white;\r\n}\r\n/*******************\r\nLabels\r\n*******************/\r\n\r\n.label {\r\n    padding: 3px 10px;\r\n    line-height: 13px;\r\n    color: $white;\r\n    font-weight: 400;\r\n    border-radius: $border-radius;\r\n    font-size: 75%;\r\n}\r\n\r\n.label-rounded {\r\n    border-radius: 60px;\r\n}\r\n\r\n.label-custom {\r\n    background-color: $cyan;\r\n}\r\n\r\n.label-success {\r\n    background-color: $success;\r\n}\r\n\r\n.label-info {\r\n    background-color: $blue;\r\n}\r\n\r\n.label-warning {\r\n    background-color: $warning;\r\n}\r\n\r\n.label-danger {\r\n    background-color: $danger;\r\n}\r\n\r\n.label-megna {\r\n    background-color: $cyan;\r\n}\r\n\r\n.label-primary {\r\n    background-color: $primary;\r\n}\r\n\r\n.label-purple {\r\n    background-color: $purple;\r\n}\r\n\r\n.label-red {\r\n   