         &:hover,\r\n            &.active {\r\n                color: $info;\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n.campaign {\r\n  position: relative;\r\n  .ct-series-a .ct-area {\r\n    fill: #4fc3f7;\r\n    fill-opacity: 0.2;\r\n  }\r\n  .ct-series-b .ct-area {\r\n    fill: #7460ee;\r\n    fill-opacity: 0.1;\r\n  }\r\n  .ct-series-a .ct-line,\r\n  .ct-series-a .ct-point {\r\n    stroke: #4fc3f7;\r\n    stroke-width: 2px;\r\n  }\r\n  .ct-series-b .ct-line,\r\n  .ct-series-b .ct-point {\r\n    stroke: #7460ee;\r\n    stroke-width: 2px;\r\n  }\r\n  .ct-series-a .ct-point,\r\n  .ct-series-b .ct-point {\r\n    stroke-width: 6px;\r\n  }\r\n}\r\n\r\n.linechart {\r\n  height:250px;\r\n}\r\n","// Loading\r\n\r\n/*******************\r\nPreloader\r\n********************/\r\n.preloader{\r\n    width: 100%;\r\n    height: 100%;\r\n    top:0px;\r\n    position: fixed;\r\n    z-index: 99999;\r\n    background: #fff;\r\n    .cssload-speeding-wheel{\r\n        \r\n    }\r\n}\r\n.lds-ripple {\r\n  display: inline-block;\r\n  position: relative;\r\n  width: 64px;\r\n  height: 64px;\r\n  position: absolute;\r\n  top: calc(50% - 3.5px);\r\n  left: calc(50% - 3.5px);\r\n    .lds-pos {\r\n      position: absolute;\r\n      border: 2px solid #2962FF;\r\n      opacity: 1;\r\n      border-radius: 50%;\r\n      animation: lds-ripple 1s cubic-bezier(0, 0.1, 0.5, 1) infinite;\r\n    }\r\n    .lds-pos:nth-child(2) {\r\n      animation-delay:-0.5s;\r\n        \r\n    }\r\n}\r\n\r\n@keyframes lds-ripple {\r\n  0% {\r\n    top: 28px;\r\n    left: 28px;\r\n    width: 0;\r\n    height: 0;\r\n    opacity: 0;\r\n  }\r\n  5% {\r\n    top: 28px;\r\n    left: 28px;\r\n    width: 0;\r\n    height: 0;\r\n    opacity: 1;\r\n  }  \r\n  100% {\r\n    top: -1px;\r\n    left: -1px;\r\n    width: 58px;\r\n    height: 58px;\r\n    opacity: 0;\r\n  }\r\n}\r\n\r\n","/*******************\r\n General Topbar\r\n*******************/\r\n\r\n.topbar {\r\n    position: relative;\r\n    z-index: 50;\r\n    transition: $transitions;\r\n    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.05);\r\n    background: $topbar;\r\n    height: $topbar-height;\r\n    .navbar-collapse {\r\n        padding: 0px 10px 0 0;\r\n    }\r\n    .top-navbar {\r\n        min-height: $topbar-height;\r\n        padding: 0px;\r\n        .dropdown-toggle::after {\r\n            display: none;\r\n        }\r\n        .navbar-header {\r\n            line-height: $topbar-navlink-height;\r\n            .navbar-brand {\r\n                display: flex;\r\n                align-items: center;\r\n                margin: 0px;\r\n                padding: $topbar-navbrand-padding;\r\n                .dark-logo {\r\n                    display: none;\r\n                }\r\n                .logo-icon{\r\n                    margin-right: 5px;    \r\n                }\r\n            }\r\n        }\r\n        .navbar-nav>.nav-item>.nav-link {\r\n           padding: $topbar-navlink-padding;\r\n           font-size: $topbar-navlink-font-size;\r\n           line-height: $topbar-navlink-height;\r\n           height:$topbar-navlink-height;       \r\n           \r\n        }\r\n        .navbar-nav>.nav-item{\r\n            transition: 0.2s ease-in;\r\n            &:hover{\r\n                background: $transparent-dark-bg;\r\n            }    \r\n        }\r\n    }\r\n    .user-dd {\r\n        min-width: 280px;\r\n    }\r\n    .nav-toggler, .topbartoggler{\r\n        color:$theme-light;\r\n        padding: 0 15px;\r\n    }\r\n}\r\n\r\n\r\n.search-box {\r\n    .app-search {\r\n        z-index: 110;\r\n        width: 100%;\r\n        top: -1px;\r\n        box-shadow: $shadow;\r\n        display: none;\r\n        left: 0px;\r\n        &.show-search {\r\n            display: block;\r\n        }\r\n        .form-control {\r\n            padding: 32px 40px 32px 23px;\r\n            @extend %square;\r\n            transition: $transitions;\r\n            &:focus {\r\n                border-color: transparent;\r\n            }\r\n        }\r\n        .srh-btn {\r\n            position: absolute;\r\n            top: 23px;\r\n            cursor: pointer;\r\n            right: 20px;\r\n        }\r\n    }\r\n}\r\n\r\n\r\n\r\n/*******************\r\nDesigned dropdown\r\n******************/\r\n.topbar {\r\n    .dropdown-menu{\r\n        padding-top: 0px;\r\n        border: 0px;\r\n        box-shadow:1px 1px 15px rgba(0, 0, 0, 0.1);\r\n        &.dropdown-menu-right {\r\n            .with-arrow{\r\n                right: 0px;\r\n                >span {\r\n                    right: 20px;\r\n                    left:auto;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n/*******************\r\nTopbar Responsive\r\n******************/\r\n\r\n@media (max-width: 767px) {\r\n    .topbar .top-navbar {\r\n        .navbar-collapse.show, .navbar-collapse.collapsing {\r\n            display: block;\r\n            width: 100%;\r\n            border-top: 1px solid $border-color;\r\n        }\r\n        .navbar-nav {\r\n            flex-direction: row;\r\n            .dropdown{\r\n                position: static;\r\n            }\r\n        }\r\n        .navbar-nav>.nav-item>.nav-link{\r\n            padding: 0 10px;\r\n        }\r\n        .navbar-header{\r\n            display: flex;\r\n            align-items: center;\r\n            width: 100%;\r\n            justify-content: space-between;\r\n        }\r\n        .dropdown-menu {\r\n            position: absolute;\r\n            width: 100%;\r\n             .with-arrow{\r\n                display: none;\r\n            }\r\n        }\r\n        \r\n    }\r\n}","/*******************\r\n Breadcrumb and page title\r\n*******************/\r\n\r\n.page-breadcrumb {\r\n    padding: 20px 20px 0 20px;  \r\n    .page-title{\r\n        margin-bottom: 0px;\r\n    }\r\n    .breadcrumb {\r\n        padding: 0px;\r\n        margin: 0px;\r\n        background-color: $skin2;\r\n        font-size: 12px;\r\n        .breadcrumb-item+.breadcrumb-item::before {\r\n            content: \"\\e649\";\r\n            font-family: themify;\r\n            color: $gray-400;\r\n            font-size: 11px;\r\n        }\r\n    }\r\n}\r\n","// Sidebar\r\n$sidebar-link-padding:14px 15px;\r\n$sidebar-icon-size:23px;\r\n$sidebar-first-level-padding:0 0 10px 0;\r\n$sidebar-first-level-link-padding: 10px 15px;\r\n$sidebar-first-level-icon-size:14px;\r\n\r\n/*******************\r\nMain sidebar\r\n******************/\r\n\r\n.left-sidebar {\r\n    position: absolute;\r\n    width: $sidebar-width-full;\r\n    height: 100%;\r\n    top: 0px;\r\n    \r\n    z-index: 10;\r\n    background: $sidebar;\r\n    box-shadow: $shadow;\r\n    transition: 0.2s ease-in;\r\n}\r\n\r\n/*******************\r\nsidebar navigation\r\n******************/\r\n.scroll-sidebar {\r\n    height: calc(100% - 20px);\r\n    position: relative;\r\n}\r\n\r\n.scroll-insider-sidebar {\r\n    height: 100%;\r\n    position: relative;\r\n}\r\n\r\n.sidebar-nav {\r\n    ul {\r\n       .sidebar-item {\r\n           width: $sidebar-width-full;\r\n           .sidebar-link {\r\n                color: $sidebar-text;\r\n                padding:$sidebar-link-padding;\r\n                display: flex;\r\n                white-space: nowrap; \r\n                align-items: center;\r\n                line-height: 25px;\r\n                opacity: 0.6;\r\n                i {\r\n                    font-style: normal;\r\n                    width: 35px;\r\n                    line-height: 25px;\r\n                    font-size: $sidebar-icon-size;\r\n                    color: $sidebar-icons;\r\n                    display: inline-block;\r\n                    text-align: center;\r\n                }\r\n                &.active,\r\n                &:hover {\r\n                    opacity: 1;\r\n                }\r\n            }\r\n           &.active > .sidebar-link{\r\n               /*background: rgba(0, 0, 0, 0.04);*/\r\n               opacity: 1;\r\n           }\r\n           /*******************\r\n           First level\r\n           ******************/\r\n           .first-level {\r\n                padding: $sidebar-first-level-padding;\r\n               .sidebar-item.active .sidebar-link{\r\n                   opacity: 1;\r\n               }\r\n                .sidebar-item .sidebar-link {\r\n                    padding: $sidebar-first-level-link-padding;\r\n                    i{\r\n                        font-size:$sidebar-first-level-icon-size;\r\n                    }\r\n                }\r\n            }\r\n        }\r\n\r\n        .sidebar-logout-item {\r\n            width: $sidebar-width-full;\r\n            .sidebar-link {\r\n                 color: #fff;\r\n                 padding:$sidebar-link-padding;\r\n                 display: flex;\r\n                 white-space: nowrap; \r\n                 align-items: center;\r\n                 line-height: 25px;\r\n                 opacity: 0.6;\r\n                 i {\r\n                     font-style: normal;\r\n                     width: 35px;\r\n                     line-height: 25px;\r\n                     font-size: $sidebar-icon-size;\r\n                     color: #fff;\r\n                     display: inline-block;\r\n                     text-align: center;\r\n                 }\r\n                 &.active,\r\n                 &:hover {\r\n                     opacity: 1;\r\n                 }\r\n             }\r\n            &.active > .sidebar-link{\r\n                /*background: rgba(0, 0, 0, 0.04);*/\r\n                opacity: 1;\r\n            }\r\n            /*******************\r\n            First level\r\n            ******************/\r\n            .first-level {\r\n                 padding: $sidebar-first-level-padding;\r\n                .sidebar-item.active .sidebar-link{\r\n                    opacity: 1;\r\n                }\r\n                 .sidebar-item .sidebar-link {\r\n                     padding: $sidebar-first-level-link-padding;\r\n                     i{\r\n                         font-size:$sidebar-first-level-icon-size;\r\n                     }\r\n                 }\r\n             }\r\n         }\r\n        /*******************\r\n         Small cap\r\n         ******************/\r\n        .nav-small-cap {\r\n            font-size: 12px;\r\n            padding:$sidebar-link-padding;\r\n            white-space: nowrap;\r\n            display: flex;\r\n            align-items: center;\r\n            line-height: 30px;\r\n            margin-top: 10px;\r\n            color: $sidebar-text;\r\n            opacity: 0.3;\r\n            text-transform: uppercase; i{line-height: 30px; margin:0 5px;}\r\n        }\r\n\r\n        .item-name {\r\n            color: #9f18fd;\r\n            font-weight: 700;\r\n        }\r\n    }\r\n    >#sidebarnav >.sidebar-item >.sidebar-link:hover{\r\n        /*background: rgba(0, 0, 0, 0.04);*/\r\n    }\r\n}\r\n/*******************\r\n common m-p removed\r\n******************/\r\n.sidebar-nav{\r\n    ul {\r\n        margin: 0px;\r\n        padding: 0px;\r\n        li {\r\n            list-style: none;\r\n        }\r\n    }\r\n}\r\n.collapse.in {\r\n    display: block;\r\n}\r\n/*******************\r\nDown arrow\r\n******************/\r\n.sidebar-nav .has-arrow {\r\n    position: relative;\r\n    &::after {\r\n        position: absolute;\r\n        content: '';\r\n        width: 7px;\r\n        height: 7px;\r\n        border-width: 1px 0 0 1px;\r\n        border-style: solid;\r\n        border-color: $sidebar-text;\r\n        margin-left: 10px;\r\n        -webkit-transform: rotate(135deg) translate(0, -50%);\r\n        -ms-transform: rotate(135deg) translate(0, -50%);\r\n        -o-transform: rotate(135deg) translate(0, -50%);\r\n        transform: rotate(135deg) translate(0, -50%);\r\n        -webkit-transform-origin: top;\r\n        -ms-transform-origin: top;\r\n        -o-transform-origin: top;\r\n        transform-origin: top;\r\n        top: 26px;\r\n        right: 